import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import {WEB_LINK} from './Global';

//RESOURCES
import { BsHouseDoor, BsChatSquareQuote } from "react-icons/bs";
import {RiStore2Line, RiHome7Line, RiInboxLine} from 'react-icons/ri';
import Logo from '../styles/resources/icons/Logo.svg';

function changeNavigationBarStatus(page, ele) {
  ele.setState({active: page});
} 

class Navigation extends Component {
    constructor(props) {
      super(props)
    
      this.state = {
        active: window.location.hash.replace("#","").replace("/","")
      }
    }

    render() {
      return(
        <header>
          <nav className="nav-bar nav-toggle" id="nav-bar">
            <div className="left-menu">
                <img className="nav-header" src={Logo} alt="logo" />
            </div>
            <div className="center-menu">
                <div className="nav-menu" id="home">
                    <Link className={this.state.active === '' ? 'menu-link active' : 'menu-link'} onClick={() => {changeNavigationBarStatus('', this)}} to='/'><RiHome7Line className="nav-icon"/></Link>
                </div>
                <div className="nav-menu" id="news">
                    <Link className={this.state.active === 'confession' ? 'menu-link active' : 'menu-link'} onClick={() => {changeNavigationBarStatus('confession', this)}} to='/confession'><RiInboxLine className="nav-icon"/></Link>
                </div>
            </div>
            <div className="right-menu">
            </div>
          </nav>
        </header>
      )
    }
}

export default Navigation;